//myAccount styles stay here
.myAccount {
    h1 {
        font-size: 1.500em;
        text-align: center;
        text-transform: uppercase;
        margin-top: 4em;

        span {
            color: $light-blue;
        }
    }
    h6 {
        font-size: 0.875em;
        text-align: left;
        text-transform: uppercase;
        margin-bottom: 1.250em;
    }
}
.interests {
    display: flex;
    flex-flow: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-top: 3.750em;
    .button-row {
        button {
            @extend .outline-button;
            padding: 0.375em 1.125em;
            margin-right: 0.875em;
        }
    } 
    .button-footer {
        display: flex;
        flex-flow: column;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        margin-top: 3.25em;
        .xl-button {
            padding: 1em 6.250em;
            margin: 0.843em 0;
            max-width: 15em;
        }
    } 
}