//button styles stay here
.outline-button {
    @extend .btn-reset;
    border-radius: 1em;
    border: 1px solid;
    @extend .lato;
    font-size: 0.875em;
    text-transform: uppercase;
    &:hover {
        opacity: 0.8;
    }
}
.xl-button {
    @extend .btn-reset;
    background: $light-blue;
    border-radius: 0;
    @extend .lato;
    font-size: 1em;
    text-transform: uppercase;
    color: $white;
    &:hover {
        background: $light-blue;
        color: $white;
        opacity: 0.8;
    }
}
.ghost-button {
    @extend .btn-reset;
    @extend .lato;
    font-size: 1em;
    text-transform: uppercase;
    color: $light-blue;
    &:hover {
        opacity: 0.8;
        text-decoration: underline;
    }
}