//news styles stay here
.news-feed {
    .news-category {
        @extend .lato;
        font-size: 0.625em;
        text-transform: uppercase;
    }
}
.news-block {
    position: relative;
    .news-content {
        position: absolute;
    }
    a {
        color: $black;
        text-decoration: none;
        display: block;
        width: 100%;
        height: 100%;
        &:hover {
            opacity: 0.8;
        }
    }
    img {
        max-width: 100%;
    }
    .main-img {
        margin-bottom: 0.938em;
        transition: all .2s ease-in-out;
        &:hover {
            transform: scale(1.01);
        }
    }
    h1 {
        font-size: 1.063em;
        margin-bottom: 0.625em;
    }
    .author-wrap {
        display: flex;
        flex-flow: row;
        align-items: center;
        justify-items: flex-start;

        img {
            width: 2.813em;
            height: 2.813em;
            border-radius: 50%;
            margin-right: 0.625em;
        }

        span {
            color: $light-gray;
            font-size: 0.813em;
            font-style: italic;
            word-break: break-all;
        }
    }
    .description {
        color: $medium-gray;
        font-size: 0.875em;
        padding: 1.250em 0;
    }
}
.main-category {
    .news-block {
        .news-content {
            h1 {
                font-size: 1.813em;
            }
        }
    }
}
.news-feed {
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    &[data="default"] {
        
        .main-category {
            height: 40em;
            
            @media screen and (max-width:991px){
                height: 45em;
            }
            @media screen and (max-width:750px){
                height: 40em;
            }
        }
        .secondary-category {
            @media screen and (max-width:991px){
                height: 25em;
            }
            @media screen and (max-width:750px){
                .news-content {
                    .main-img {
                        display: none;
                    }
                }
            }
        }
        .news-block{
            .news-content {
                opacity: 0;
                visibility: hidden;
                transition: opacity 2s ease-in-out;
                &.show {
                    opacity: 1;
                    visibility: visible;
                }
                .description {
                    @media screen and (min-width:750px){
                        display: none;
                    }
                }
            }
        }
    }
    &[data="extra"] {
        margin-bottom: 6.250em;
        .news-block{
            .news-content {
                .main-img {
                    display: none;
                }
            }
        }
    }
}
.col-md-3,
.col-md-4 {
    @media screen and (max-width:991px){
        height: 15em;
    }
    @media screen and (max-width:750px){
        height: 23em;
    }
    .news-block {
        .news-content {
            opacity: 0;
            visibility: hidden;
            &:nth-of-type(1) {
                opacity: 1;
                visibility: visible;
            }
        }
    }
}