//form styles stay here
.default-form {
    .group {
        display: flex;
        flex-flow: column;
        align-items: flex-start;
        align-items: flex-start;
        margin-bottom: 1.250em;

        label {
            @extend .lato;
            font-size: 0.875em;
            text-transform: uppercase;
        }

        input {
            @extend .lato;
            color: $medium-gray;
            font-size: 0.8em;
            height: 2.500em;
            border: 1px solid $lighter-gray;
            width: 100%;
            padding: 0.5em;
        }
    }

}