//resets stay here

.list-reset {
    ol, ul {
        list-style: none; }
}
.btn-reset {
    -webkit-appearance: none;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
    border: none;
    margin: 0;
    padding: 0;
    width: auto;
    overflow: visible;
    background: transparent;

    &:hover {
        outline: 0;
        color: inherit;
        background: inherit;
    }

    &::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
}