//menu styles stay here
.main-nav {
    @extend .list-reset;
    margin: 0;
    min-height: auto;

    ul {
        display: flex;
        align-items: flex-start;
        justify-content: flex-end;

        .nav-item {
            margin: 0 0.938em;
            &:last-child {
                margin-right: 0;
            }

            .nav-link {
                @extend .lato;
                @extend .btn-reset;
                font-size: 0.875em;
                text-transform: uppercase;
                color: inherit;
                cursor: pointer;

                &.login {
                    color: $light-blue;
                }
                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
}
.main-nav--mobile {
    width: 100%;
    height: auto;
    position: absolute;
    top: 57px;
    left: 0;
    padding: 0;
    .navbar-collapse {
        position: relative;
        top: -0.5em;
        z-index: -3;
        opacity: 0;
        &.show {
            background: $white;
            top: 0;
            z-index: 2;
            opacity: 1;
            transition: top .1s;
            height: 100vh;
        }
    }
    ul {
        flex-flow: column;
        .nav-item {
            border-bottom: 1px solid #fff;
            width: 100%;
            margin: 0;
            padding: 0.938em 1.563em;

            &:nth-of-type(1) {
                padding-top: 1.563em;
            }
        }
    } 
}
.mobile-toggle {
    img {
        width: 1.500em;
        cursor: pointer;
    }
}