//helpers stay here
.hidden-sm {
    @media screen and (max-width:991px){
        display: none;
    }
}

.visible-sm-only {
    display: none;

    @media screen and (max-width:991px){
        display: block;
    }
}

.separator {
    width: 100%;
    height: 1px;
    border-top: 1px solid #E4E4E4;
    margin-bottom: 1.875em; 
    @media screen and (max-width:991px){
        display: none;
    }
}

.hidden {
    display: none;
}

.invisible {
    visibility: hidden;
}

.wrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    background: $white;
    top: 4em;
    left: 0;
    right: 0;
    bottom: 0;
}